#error-page {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;
  width: 90%;
  height: 100%;
  margin: auto;
  padding: 0;
  color: $black;
  text-shadow: 1px 1px 2px $white;


  h1 {
    font-size: 3rem;
    font-weight: 700;
    text-align: center;
    margin: 0;
    padding: 0;
  }


      .portrait {
        border-radius: 50%;
        display: block;
        margin: auto;
        max-width: 70%;
        max-height: 70%;
        }

        .linkOut{
          color: $black;
          font-size: 1.5rem;
          font-weight: 700;
          text-align: center;
          margin: 1.5rem;
          transition: .2s;
          &:hover {
            color: $selected;
          }
          &:after{
            content: ">>>>>>>";
            display: inline-block;
          }
         
        }
  
  @media screen and (max-width: 900px) {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;


    section {
      width: 100%;


      .left {
        width: 100%;
        margin: 1rem;

      }

      .right {
        width: 100%;

        .portrait {
          border-radius: 50%;
          display: block;
          margin: 0 auto;
          max-width: 30%;
          }

      }
    }
  }

  @media screen and (max-width: 500px) {

        .portrait {
          max-width: 50%;
          }

  }
  
}







