  #project {
    width: 90%;
    height: 100%;
    margin: auto;
    padding: 0;
    color: $black;
    text-shadow: 1px 1px 2px $white;
    margin: 3rem auto 1rem auto;

    h1 {
      font-size: 3rem;
      line-height: 3rem;
      font-weight: 700;
      text-align: center;
      margin: 2rem auto;
      padding: 0;
      width: 100%;
    }


    /* Style the buttons */
    .btn {
      text-align: center;
      padding: 0.3rem;
      margin: 0.3rem;
      background-color: white;
      border: 1px solid black;
      border-radius: 5px;
      cursor: pointer;
    }

    .btn:hover {
      border: 1px solid rgb(23, 190, 187);
    }

    .btn.active {
      background-color: #666;
      color: white;
    }



 // slider css
 // source : https://codepen.io/alvarotrigo/pen/bGqgqeK

    .project__list__container {
      // display: flex;
      // flex-wrap: nowrap;
      // justify-content: left;
      // align-items: center;
      // margin: 0 auto;
      // overflow-x: scroll;

      /* slider css */
      display: flex;
      overflow-x: scroll;
      position: relative;
      scroll-behavior: smooth;
      scroll-snap-type: x mandatory;

      /* Three columns side by side */
      .project__list__item {
        // margin: 1rem;
        // border-radius: 10px;
        // overflow: hidden;
        // min-width: 30%;
        // height: 300px;
        // border-radius: 25px;
        // margin-top: 0.5rem;

        /*slider css */
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        width: 100%;
        height: 400px;
        scroll-snap-align: center;
        margin-right: 0px;
        box-sizing: border-box;
        background: white;
        transform-origin: center center;
        transform: scale(1);


        @media screen and (max-width: 969px) {
          min-width: 45%;
        }

        @media screen and (max-width: 649px) {
          //min-width: 600px;
          min-width: 85%;
        }


        &:hover {
          transition: all 1s ease-in-out;
          box-shadow: 0 0 10px #333;

          .project__list__item--card--cover {
            box-shadow: 0 0 10px #333;
            border-radius: 5px;
            animation: mymove 5s infinite;
            transition: all 0.3s ease-in-out;
          }

          @keyframes mymove {
            0% {
              background-position: left top
            }

            25% {
              background-position: center;
            }

            100% {
              background-position: right bottom;
            }
          }
        }

        .card__link {


          /* Add some shadows to create a card effect */
          .project__list__item--card {
            display: block;
            width: 100%;
            height: 100%;
            overflow: hidden;
            border-radius: 5px;
            background: #ffffff;
            box-shadow: 20px 20px 60px #bebebe, -20px -20px 60px #ffffff;
            transition: all 1s ease-in-out;


            

/* slider css  */
 a {
  background: none;
  border: none;
}

a.slide__prev,
.project__list__item::before {
  transform: rotate(135deg);
  -webkit-transform: rotate(135deg);
  left: 5%;
}

a.slide__next,
.project__list__item::after {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  right: 5%;
}

.project__list__item::before,
.project__list__item::after,
.slide__prev,
.slide__next {
  position: absolute;
  top: 48%;
  width: 35px;
  height: 35px;
  border: solid black;
  border-width: 0 4px 4px 0;
  padding: 3px;
  box-sizing: border-box;
}

.project__list__item::before,
.project__list__item::after {
  content: "";
  z-index: 1;
  background: none;
  pointer-events: none;
}

/* END slider css */ 


            .project__list__item--card--cover {
              width: 90%;
              height: 80%;
              background-size: cover;
              background-position: center;

              max-width: 90%;
              max-height: 90%;
              margin: 0.5rem auto 0;
              border: 3px solid white;
              outline: 3px solid white;

              border-radius: 5px;
              box-shadow: 0 0 10px rgba(0, 22, 44, 0.5);

              &:hover {
                transform: scale(1.12);
              }
            }

            /* Some left and right padding inside the container */
            .project__list__item--card--content {
              width: 100%;
              height: 20%;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              text-align: center;




              .title {
                color: grey;
              }

              .button {
                border: none;
                outline: 0;
                display: inline-block;
                padding: 8px;
                color: white;
                background-color: #000;
                text-align: center;
                cursor: pointer;
                width: 100%;
              }

              .button:hover {
                background-color: #555;
              }
            }
          }
        }

        // }

      }
    }


    /* page descriptif de chaque projet */
    .project__description {
      border-radius: 5px;
      box-shadow: 0 0 10px rgba(0, 22, 44, 0.5);
      width: 98%;
      padding: 1rem;
      margin: auto;
      margin-top: 1rem;

      h2 {
        margin-top: 1rem;
        margin-bottom: 1rem;
        font-size: 2rem;
        font-weight: 700;
        text-align: left;
      }

      a {
        width: fit-content;
        display: block;
        text-decoration: underline;
        text-decoration-color: blue;
        color: $black;
      }

      p {
        text-align: left;
        line-height: 2rem;
      }

      .project__techno,
      .project__method {
        text-align: left;
        line-height: 2rem;
        display: inline-block;
        width: auto;
      }

      .project__techno--list,
      .project__method--list {
        text-align: left;
        // line-height: 2rem;
        // display: inline-block;
        width: auto;
        display: flex;
        flex-wrap: wrap;
        // justify-content: center;
      }

      .tag {
        display: inline;
        font-size: 1rem;
        font-weight: 700;
        border-radius: 5px;
        border: 2px solid $selected;
        background-color: rgb($selected, 0.2);
        padding: 0.2rem 0.5rem;
        margin: auto 0.2rem;
      }


      strong {
        font-size: 0.8rem;
        font-weight: 700;
        color: red;
      }

      .project__list--action {
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        width: 100%;
        margin: 0;
        padding: 0;
        margin-left: 0.5rem;

        li {
          display: inline-block;
          margin-left: 1rem;
          padding: 0;
          list-style: none;
        }
      }

      .tag {
        font-size: 1rem;
        font-weight: 700;
        border-radius: 5px;
        border: 2px solid $selected;
        background-color: rgb($selected, 0.2);
        margin: 0.2rem;
        padding: 0.2rem 0.5rem;
      }

      .html {
        border-color: rgb(255, 0, 0);
        background-color: rgb(255, 0, 0, 0.2);
      }

      .css {
        border-color: rgb(0, 0, 255);
        background-color: rgb(0, 0, 255, 0.2);
      }

      .sass {
        border-color: rgb(204, 102, 152);
        background-color: rgb(204, 102, 152, 0.2);
      }

      .js {
        border-color: rgb(240, 219, 79);
        background-color: rgb(240, 219, 79, 0.2);
      }

      .vuejs {
        border-color: rgb(66, 184, 131);
        background-color: rgb(66, 184, 131, 0.2);
      }

      .react {
        border-color: rgb(97, 219, 251);
        background-color: rgb(97, 219, 251, 0.2);
      }

      .axios {
        border-color: #555da7;
        background-color: rgb(#555da7, 0.2);
      }

      .php {
        border-color: rgb(137, 147, 190);
        background-color: rgb(137, 147, 190, 0.2);
      }

      .wp {
        color: rgb(68, 65, 64);
        border-color: rgb(68, 65, 64);
        background-color: rgb(0, 116, 156, 0.2);
      }

      .mysql {
        border-color: rgb(227, 143, 0);
        background-color: rgb(227, 143, 0, 0.2);
      }

      .nodejs {
        border-color: rgb(68, 136, 62);
        background-color: rgb(68, 136, 62, 0.2);
      }

      .express {
        border-color: rgb(0, 153, 0);
        background-color: rgb(0, 153, 0, 0.2);
      }

      .mongodb {
        border-color: rgb(0, 104, 74);
        background-color: rgb(0, 104, 74, 0.2);
      }

      .github {
        border-color: rgb(0, 0, 0);
        background-color: rgb(0, 0, 0, 0.2);
      }

      .photoshop {
        border-color: rgb(48, 198, 238);
        background-color: rgb(48, 198, 238, 0.2);
      }
    }

    .imgDiapo {
      display: flex;
      flex-direction: column;
      flex-wrap: nowrap;
      align-items: center;

      .imgDiapo__p {
        text-align: center;
        font-weight: 700;
        margin: 1rem auto 0 auto;
      }

      .imgDiapo__img {
        border-radius: 25px;
        box-shadow: 1px 1px 10px rgba(0, 22, 44, 0.5);
        max-width: 70%;
        max-height: 500px;
        margin: 0.5rem auto 1rem auto;
      }
    }



    @media screen and (max-width: 900px) {
      .project__description {
        width: 100%;
        padding: 0.5rem;
        margin: auto;
        margin-top: 1rem;

        h2 {
          margin-top: 1rem;
          margin-bottom: 1rem;
          font-size: 3rem;
          font-weight: 700;
          text-align: center;
        }


        p,
        .project__techno,
        .project__method,
        a {
          text-align: center;
          line-height: 2rem;
          margin: 1rem auto;
          width: 100%;
        }

        .project__techno--list,
        .project__method--list {
          text-align: center;
          line-height: 1rem;
          display: flex;
          justify-content: center;
          flex-wrap: wrap;
          width: 100%;
        }

        .project__list {
          display: flex;
          justify-content: center;
          flex-wrap: wrap;
          width: 100%;
        }

      }
    }
  }