.connection-form {
  border: 2px solid $selected;
  width: 90%;
  margin: 35px auto 0;
  padding: 20px 0;
  user-select: none;
  text-align: center;

  h3 {
    padding: 10px 0 15px;
  }

  button {
    display: flex;
    justify-content: space-around;
      background: $color-1;
      color: $white;
      width: 10rem;
      margin: 0.3rem;
      padding: 1rem 0;
      border-radius: 18px;
      cursor: pointer;
      transition: 0.3s;
      box-shadow: 3px 3px 1px rgba(51, 51, 51, 0.418);

      &:hover {
        background: $selected;
      }
    
  }
  .active-btn {
    background: $selected;
  }
  form {
    width: 400px;
    margin: 10px auto 0;
    padding: 20px;

  }
}

